import * as XLSX from 'xlsx';
import { getBase64 } from "../file-helper"

const validateNetwork = async (format, f, ID, network_sheet = 'template_network') => {
  const errors = {
    sheet: [],
    sender_id: [],
    values: [],
    empty: [],
    labels: [],
  }

  let error = false;

  const N = ID.length

  // Verify values
  // (nj is a global value from numjs script)
  // eslint-disable-next-line no-undef
  let A = nj.zeros([N, N])

  if (format !== 'xlsx' && format !== 'xls') {
    throw new Error("Wrong format file");
  } else {
    const f64 = await getBase64(f);
    const file = XLSX.read(f64, { type: 'base64' });
    const row_header_values = [];
    const column_values = [];

    const net = file.Sheets[network_sheet];

    if (!net || net.length == 0) {
      errors.sheet.push(`The file uploaded do not contain a sheet with the name: ${network_sheet}`);
      error = true;
    } else {
      let range = '';
      if (N === 0) {
        // If doesn't exist array names will be set by numbers from 1 to array indicators length
        range = XLSX.utils.decode_range(net['!ref'])
        for (var r = (range.s.r + 1); r <= range.e.r; r += 1) {
          net[XLSX.utils.encode_cell({ c: 0, r: r })]['v'] = r
        }
      } else {
        var cont = 0;
        console.log(net);
        // Verify if indicators names are correctly
        Object.keys(net).map((k) => {
          const re_A = new RegExp("^A[0-9]");
          const row_headers = new RegExp("^[A-Z]+1");
          if (row_headers.test(k) && !new RegExp("^A[0-9]").test(k) && net[k]['t'] === 's') {
            row_header_values.push(net[k]['v']);
          }
          if (re_A.test(k) && k !== 'A1') {
            if (net[k]['v'] === ID[cont]) {
              column_values.push(net[k]['v']);
              cont += 1;
            } else {
              errors.sender_id.push(`Cell ${k} has an error`)
              cont += 1
              error = true;
            }
          }
        });

        if (row_header_values !== column_values) {
          errors.values.push(`The rows do not match the column values in the matrix`);
        }

        // Convert to NdArray matrix and doing the diagonal zero
        range = XLSX.utils.decode_range(net['!ref']);

        try {
          for (var r = (range.s.r + 1); r <= range.e.r; r += 1) {
            for (var c = (range.s.c + 1); c <= range.e.c; c += 1) {
              if (r !== c) {
                if (net[XLSX.utils.encode_cell({ c: c, r: r })] !== undefined) {
                  if (isNaN(net[XLSX.utils.encode_cell({ c: c, r: r })]['v'])) {
                    errors.values.push(XLSX.utils.encode_cell({ c: c, r: r }))
                    error = true;
                  } else {
                    A.set((r - 1), (c - 1), net[XLSX.utils.encode_cell({ c: c, r: r })]['v'])
                  }
                }
              }
            }
          }
        } catch(err) {
          errors.empty.push('The uploaded file structure does not meet the template format or is empty');
          error = true;
        }
      }
    }
  }

  const matrix_is_not_empty = A.tolist().some(i => i.some(v => v !== 0));

  if (!Object.values(errors).filter(i => i.length > 0).length &&
      matrix_is_not_empty) {
    return { A }
  } else {
    if (errors.sender_id.length > 10) {
      errors.labels.push('The uploaded file structure does not meet the template format or the label indicators \
        do not match the label indicators from the file uploaded before');
    }
    if(!matrix_is_not_empty && !errors.empty.length && !errors.sheet.length) {
      errors.empty.push('The uploaded file structure does not meet the template format or is empty');
      error = true
    }
    return { errors, error }
  }
}

export { validateNetwork };
