import React from 'react';
import { Box, Button, Container, Grid, Link, Typography } from '@mui/material';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import networkTemplate from '../../../assets/templates/template_network.xlsx';
import styles from './steps.module.scss';
import { texts_network as texts } from '../../../global/texts/texts-network-page.js'
import { CustomBreadcrumbs } from '../breadcrumbs';
import { MediaUploader } from '../../../common/media-uploader/media-uploader';
import { FileValidation } from './validations/fileValidation';
import { getFileExtention } from '../../../helpers/file-helper';
import { validateNetwork } from '../../../helpers/validations/validateNetwork';
import { SiteTitle } from '../../../components/SiteTitle';

const Network = (props) => {
  const { nextStep, simulationState, networkFile, setNetworkFile, setNetworkResults, indicatorsResult } = props;
  const [verified, setVerified] = React.useState(false);
  const [validationResult, setValidationResult] = React.useState(null);
  const { ID } = indicatorsResult;
  const uploadFile = async (file) => {
    if (file && file.name) {
      const extention = getFileExtention(file);
      const fileValidation = await validateNetwork(extention, file, ID);
      setNetworkFile(file);
      if (
        !fileValidation.error
      ) {
        setVerified(true);
        setValidationResult(fileValidation);
        setNetworkResults(fileValidation);
      } else {
        setVerified(false);
        setValidationResult(fileValidation);
      }
    } else {
      setNetworkFile([]);
      setVerified(false);
      setValidationResult(null);
    }
  }
  return (
    <Container
      maxWidth="xl"
      sx={{
        paddingY: (theme) => [ theme.spacing(4), theme.spacing(4), theme.spacing(8) ]
      }}
    >
      <Grid container rowSpacing={4} columnSpacing={8} columnns={{ xs: 4, sm: 8, md: 12, lg: 12 }}>
        <Grid item xs={12} md={5}>
          <SiteTitle />
        </Grid>
        <Grid item xs={12} md={7}>
          <CustomBreadcrumbs selected={simulationState} />
        </Grid>
        <Grid item xs={12} md={5}>
          <Typography variant='h1'>
            Network
          </Typography>
          <Typography mt={4}>
            { texts.INFORMATION_DESCRIPTION }
          </Typography>
          <Link
            href={ texts.ARTICULE_LINK }
            target="_blank"
            rel="noopener referrer"
          >
            { texts.ARTICULE_LINK }
          </Link>
        </Grid>
        <Grid item xs={12} md={7}>
          <Typography mb={2}>
            { texts.TEMPLATE_DESCRIPTION }
          </Typography>
          <Button
            variant="outlined"
            href={networkTemplate}
          >
            Download template
          </Button>
          <MediaUploader
            className={styles.mediaUploader}
            maxFiles={1}
            previewsPerLine={1}
            supportedExtensions={['xlsx', 'xls']}
            onFileSelection={(files) => {
              if (files) {
                uploadFile(files[0]);
              } else {
                uploadFile([]);
              }
            }}
          />
          {networkFile
          && validationResult
            && (
              <FileValidation verified={verified} component="network" validationResult={validationResult} />
            )}
          <Box mt={4} textAlign="right">
            <Button
              variant='outlined'
              size='large'
              onClick={() => nextStep()}
              sx={{
                marginRight: (theme) => theme.spacing(4)
              }}
            >
              Skip this step
            </Button>
            <Button
              variant='contained'
              size='large'
              onClick={() => nextStep()}
              disabled={!verified}
              endIcon={<NavigateNextIcon />}
            >
              Next
            </Button>
          </Box>
        </Grid>
      </Grid>
    </Container>
  )
}

export { Network };

export default Network;